* {
  box-sizing: border-box;
}

body {
  text-align: center;
  background-color: #eeeeee;
}

.Row {
  display: flex;
  align-items: center;
  justify-content: center;
}

.Row-separator.Row-letter {
  background-color: rgb(87, 172, 120);
  flex: 1;
  max-width: 40px;
  height: 2px;
  font-size: 28px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-weight: bold;
  margin: 10px 0px 10px 0px;
  border: 0px;
}

.App-container.color-blind .Row-separator {
  background-color: #85c0f9;
}

.NextPrev {
  font-weight: bold;
  font-size: 40px;
  line-height: .5;
}

.DayNum {
  font-size: 18px;
}

.Row-letter {
  margin: 2px;
  border: 2px solid rgba(128, 128, 128, 0.8);
  flex: 1;
  max-width: 40px;
  height: 40px;
  font-size: 28px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-weight: bold;
}

.Row-letter-color {
  margin: 2px;
  border: 2px solid rgba(128, 128, 128, 0.8);
  flex: 1;
  max-width: 40px;
  height: 40px;
  font-size: 28px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-weight: bold;
}

.Row-annotation {
  margin-inline-start: 16px;
  width: 5em;
  text-align: end;
  margin-left: -80px;
  padding-left: 80px;
}

.App-container {
  position: relative;
  max-width: 500px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  justify-content: center;
}

.App-container h1 {
  margin-top: 0;
  margin-bottom: 5px;
}

.Game,
h1 {
  user-select: none;
}

.Game {
  display: flex;
  flex-direction: column;
}

table.Game-rows {
  margin: auto;
  display: flex;
  flex-direction: column;
}

table.Game-rows:focus {
  outline: none;
}

table.Game-rows > tbody {
  display: flex;
  flex-direction: column;
}

.Game-keyboard {
  display: flex;
  flex-direction: column;
}

.Game-keyboard-row {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
}

.Game-keyboard-button {
  margin: 2px;
  background-color: #cdcdcd;
  padding: 2px;
  text-transform: capitalize;
  border-radius: 4px;
  min-height: 40px;
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
  font-family: inherit;
  font-size: 20px;
  color: inherit;
  text-decoration: inherit;
  border: 2px solid transparent;
  cursor: pointer;
}

.Game-keyboard-button-wide {
  flex: 2;
}

.Game-keyboard-button:focus {
  outline: none;
}

.letter-correct, .letter-correct-fin {
  border: 2px solid rgba(0, 0, 0, 0.3);
  background-color: rgb(87, 172, 120);
  color: white !important;
}

.letter-elsewhere, .letter-elsewhere-fin {
  border: 2px solid rgba(0, 0, 0, 0.3);
  background-color: rgb(233, 198, 1);
  color: white !important;
}

.letter-correct-fin {
  border: 2px solid rgba(0, 0, 0, 0.3);
  background-color: rgba(87, 172, 120);
  color: white !important;
}

.letter-elsewhere-fin {
  border: 2px solid rgba(0, 0, 0, 0.3);
  background-color: rgba(233, 198, 1);
  color: white !important;
}

.letter-absent, 
.Game-keyboard-button.letter-correct-fin,
.Game-keyboard-button.letter-elsewhere-fin,
.App-container.color-blind .Game-keyboard-button.letter-correct-fin, 
.App-container.color-blind .Game-keyboard-button.letter-elsewhere-fin {
  border: 2px solid transparent;
  background-color: rgb(162, 162, 162);
  color: white !important;
}

.Typing-known-absent {
  border: 1px dotted white;
}

.Typing-known-elsewhere{
  border: 1px dotted rgba(233, 198, 1);
}

.Typing-known-correct {
  border: 1px solid rgba(87, 172, 120);
}

body.dark {
  background-color: #202020;
  color: #e0e0e0;
  color-scheme: dark;
}

body.dark .Game-keyboard-button {
  color: #404040;
}

a,
a:visited {
  color: #8080ff;
}

a:active {
  color: #cc77ff;
}

.Game-options {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 0.5rem;
}

.Game-options > * + * {
  margin-inline-start: 0.5rem;
}

.Game-options button {
  min-width: 4rem;
}

.Game-name-mode-container {
}

.Game-name {
  color: inherit;
  font-style: inherit;
  margin-right: 5px;
  display: inline-block;
}

.Game-modes {
  box-sizing:unset;
  margin-top: -10px;
}

.Game-stats {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 16px 0;
  width: 80%;
}

.Game-stats h1 {
  font-weight: 700;
  font-size: 16px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  text-align: center;
}

.Game-stats-games {
  display: flex;
  flex-direction: row;
  padding-bottom: 10px;
}

.stat {
  flex: 1;
}

.stat-num {
  font-size: 36px;
  font-weight: 400;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  letter-spacing: 0.05em;
  font-variant-numeric: proportional-nums;
}

.stat-label {
  font-size: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.Game-stats-guesses {
  padding-bottom: 10px;
  width: 80%;
}

.guess-stat {
  width: 100%;
  height: 20px;
  display: flex;
  align-items: center;
  padding-bottom: 4px;
  font-size: 14px;
  line-height: 20px;
}

.guess-count {
  font-size: 14px;
  height: 20px;
  line-height: 20px;
}

.guess-graph {
